<template>
	<view>
		<uv-toast ref="toast"></uv-toast>
		<wm-list-add ref="add" @clickAdd="clickAdd" />
		<!-- category-list：分类列表  @itemClick：条目点击 -->
		<cc-beautyCate :category-list="categoryList" @itemClick="toCategory"></cc-beautyCate>
	</view>
</template>
<script>
	import {
		Static
	} from "vue"
	// 导入侧边添加组件
	import wmListAdd from '@/components/wm-list-add/wm-list-add';
	export default {
		components: {
			wmListAdd
		},
		data() {
			return {
				categoryList: [{
						cid: 1,
						main_name: '1号食堂',
						data: [{
								next_name: '素菜',
								info: [{
										son_name: '生菜',
										imgurl: "../../static/square_fill.png",
										status: 1
									},
									{
										son_name: '卷心菜',
										imgurl: "../../static/square_fill.png",
										status: 1
									},
									{
										son_name: '茄子',
										imgurl: "../../static/square_fill.png",
										status: 1
									},
									{
										son_name: '玉米',
										imgurl: "../../static/square_fill.png",
										status: 1
									},
									{
										son_name: '土豆',
										imgurl: "../../static/square.png",
										status: 0
									},
								],
							},
							{
								next_name: '肉类',
								info: [{
										son_name: '牛肉',
										imgurl: "../../static/square_fill.png",
										status: 1
									},
									{
										son_name: '鱼',
										imgurl: "../../static/square.png",
										status: 0
									},
									{
										son_name: '鸡肉',
										imgurl: "../../static/square_error.png",
										status: 2
									}
								]
							}
						]
					},
					{
						cid: 2,
						main_name: '2号食堂',
						data: [{
								next_name: '素菜',
								info: [{
										son_name: '玉米',
										imgurl: "../../static/square_error.png",
										status: 2
									},
									{
										son_name: '土豆',
										imgurl: "../../static/square_fill.png",
										status: 1
									},
								],
							},
							{
								next_name: '肉类',
								info: [{
									son_name: '鱼',
									imgurl: "../../static/square_fill.png",
									status: 1
								}]
							}
						]
					},
					{
						cid: 3,
						main_name: '3号食堂',
						data: [{
								next_name: '素菜',
								info: [{}]
							},
							{
								next_name: '肉类',
								info: [{}]
							}
						]
					},
					{
						cid: 4,
						main_name: '6号食堂',
						data: [{
								next_name: '素菜',
								info: [{}]
							},
							{
								next_name: '肉类',
								info: [{}]
							}
						]
					},
					{
						cid: 5,
						main_name: '10号食堂',
						data: [{
								next_name: '素菜',
								info: [{}]
							},
							{
								next_name: '肉类',
								info: [{}]
							}
						]
					}
				]
			}
		},
		methods: {
			// 侧边添加
			clickAdd() {
				var addData = {son_name:'未命名',imgurl:'../../static/square.png',status:0};
				var vegetable = this.categoryList[0].data[0];
				var meat = this.categoryList[0].data[1];
				console.log('点击加号执行操作');
				uni.showActionSheet({
					itemList: ['添加素菜留样柜', '添加肉类留样柜'],
					success: function (res) {
						if(res.tapIndex===0){
							console.log('选中了第' + (res.tapIndex + 1) + '个按钮');
							vegetable.info.push(addData);
							
						}else{
							console.log('选中了第' + (res.tapIndex + 1) + '个按钮');
							meat.info.push(addData);
						}
					},
					fail: function (res) {
						// console.log(res.errMsg);
					}
				});
			},
			// 分类条目点击
			toCategory(item) {
				if (item.status === 0) {
					uni.navigateTo({
						url: '/pages/information/information'
					})
				} else if (item.status === 1) {
					uni.showToast({
						title: '已留样',
						duration: 1000
					})
				} else {
					uni.showModal({
						title: '提示',
						content: '该样品已过期，是否清理？',
						success(res) {
							if (res.confirm) {
								console.log('用户点击确认');
							} else if (res.cancel) {
								console.log('用户点击取消');
							}
						}
					})
				}

				// uni.showModal({
				// 	title: '测试',
				// 	content: '点击条目数据 = ' + JSON.stringify(item)
				// })
			}
		}
	}
</script>
